import React, { Component, Fragment } from 'react';
import { Link } from 'react-router-dom';
import arrow from '../../img/icon_scrolldownarrow.png';
import axios from 'axios';

class Setting extends Component {
  state = {
    easy: '',
    difficult: '',
    team: '1',
    list: [],
    categoryId: '',
    time: 30,
  };

  componentDidMount() {
    const url = 'http://192.168.0.10:8080/nullBox';
    axios

      .get(
        url + `/api/category/1`
      )
      .then(r => {
        this.setState({ list: r.data });
      });
  }

  category = event => {
    let target = event.target;
    let id = target.id;

    if (target.style.color === 'yellow') {
      target.style.color = '';
    } else {
      target.style = 'color : yellow';
    }
  };

  game1 = event => {
    localStorage.setItem('difficulty', 1);
    let target = event.target;
    let id = target.id;

    if (target.style.color === 'yellow') {
      target.style.color = '';
    } else {
      target.style = 'color : yellow';
    }
  };

  game2 = event => {
    localStorage.setItem('difficulty', 2);
    let target = event.target;
    let id = target.id;

    if (target.style.color === 'yellow') {
      target.style.color = '';
    } else {
      target.style = 'color : yellow';
    }
  };

  speed = () => {
    localStorage.setItem('team', this.state.team);
    localStorage.setItem('j', this.state.team);
    localStorage.setItem('t', 1);

    var arr = document.querySelectorAll('.btn-fill select');
    let i = this.state.categoryId;

    for (var j = 0; j < arr.length; j++) {
      if (arr[j].style.color === 'yellow') {
        if (i == '') {
          i += arr[j].id;
        } else {
          i += ',';
          i += arr[j].id;
        }
      }
    }
    console.log(i);

    localStorage.setItem('category', i);
    localStorage.setItem('time', this.state.time);
  };

  korean = () => {
    localStorage.setItem('team', this.state.team);
    localStorage.setItem('j', this.state.team);
    localStorage.setItem('t', 1);
    localStorage.setItem('time', this.state.time);
  };

  brand = () => {
    localStorage.setItem('team', this.state.team);
    localStorage.setItem('j', this.state.team);
    localStorage.setItem('t', 1);
    localStorage.setItem('time', this.state.time);
  };

  handleChange = event => {
    let target = event.target;
    let i = target.value;
    this.setState({ team: i });
  };

  handleChangeTime = event => {
    let target = event.target;
    let i = target.value;
    this.setState({ time: i });
  };

  render() {
    let explain = localStorage.getItem('explain');
    let param = localStorage.getItem('game');

    var s = JSON.stringify(this.state.list);
    var s1 = JSON.parse(s);

    let category = s1.map(list => (
      <button class="checkbox" id={list.id} onClick={this.category.bind(this)}>
        <div className="btn-fill select" id={list.id}>
          {list.name}
        </div>
      </button>
    ));

    let selectTeam = (
      <section class="radio-items" onChange={this.handleChange}>
        <span className="game-info">Team</span>
        <div class="btn-radio">
          <input
            id="b1"
            class="only-sr"
            type="radio"
            name="team"
            value="1"
          />
          <label for="b1">
            <span />1
          </label>
        </div>
        <div class="btn-radio">
          <input id="b2" class="only-sr" type="radio" name="team" value="2" />
          <label for="b2">
            <span />2
          </label>
        </div>
        <div class="btn-radio">
          <input id="b3" class="only-sr" type="radio" name="team" value="3" />
          <label for="b3">
            <span />3
          </label>
        </div>
        <div class="btn-radio">
          <input id="b4" class="only-sr" type="radio" name="team" value="4" />
          <label for="b4">
            <span />4
          </label>
        </div>
        <div class="btn-radio">
          <input id="b5" class="only-sr" type="radio" name="team" value="5" />
          <label for="b5">
            <span />5
          </label>
        </div>
      </section>
    );

    let selectTime = (
      <section class="radio-items" onChange={this.handleChangeTime}>
        <span className="game-info"> time</span>
        <div class="btn-radio">
          <input
            id="t1"
            class="only-sr"
            type="radio"
            name="time"
            value="30"
          />
          <label for="t1"> <span />30

          </label>
        </div>
        <div class="btn-radio">
          <input id="t2" class="only-sr" type="radio" name="time" value="60" />
          <label for="t2">  <span />1</label>
        </div>
        <div class="btn-radio">
          <input id="t3" class="only-sr" type="radio" name="time" value="90" />
          <label for="t3"> <span /> 1 30</label>
        </div>
        <div class="btn-radio">
          <input
            id="t4"
            class="only-sr"
            type="radio"
            name="time"
            value="120"
          />
          <label for="t4">  <span />2</label>
        </div>
        <div class="btn-radio">
          <input
            id="t5"
            class="only-sr"
            type="radio"
            name="time"
            value="150"
          />
          <label for="t5"> <span /> 2 30</label>
        </div>
      </section>
    );

    if (param === '') {
      return (
        <div className="setting">
          <div className="container-basic">
            <Link to="/">
              <p className="btn-top back"></p>
            </Link>
            <div className="info-text-wide">
              <div className="game-name">
                <h1>{param}</h1>
              </div>
              <div className="game-info">
                <h4>{explain}</h4>
                <br />
              </div>
              {selectTeam}
              {selectTime}
              <div className="next-btn">
                <div className="category-button">{category}</div>
              </div>
            </div>
          </div>
          <div className="btn-next">
            <Link to="/speed" onClick={this.speed}>
              
              <div>
                <img src={arrow} alt="" />
              </div>
            </Link>
          </div>
        </div>
      );
    } else if (param === '') {
      return (
        <div className="setting">
          <div className="container-basic">
            <Link to="/">
              <p className="btn-top back"></p>
            </Link>
            <div className="text-info-wide">
              <div className="game-name">
                <h1>{param}</h1>
              </div>
              <div className="game-info">
                <h4>{explain}</h4>
                <br />
              </div>
              {selectTeam}
              {selectTime}
              <div className="home-button">
                <button className="btn-fill" onClick={this.game1.bind(this)}>
                  
                </button>
                <button className="btn-fill" onClick={this.game2.bind(this)}>
                  
                </button>
              </div>
              <div className="btn-next">
                <Link to="/Brand" onClick={this.brand}>
                  
                  <div>
                    <img src={arrow} alt="" />
                  </div>
                </Link>
              </div>
            </div>
          </div>
        </div>
      );
    } else if (param === '') {
      return (
        <div className="setting">
          <div className="container-basic">
            <Link to="/">
              <p className="btn-top back"></p>
            </Link>

            <div className="info-text-wide">
              <div className="game-name">
                <h1>{param}</h1>
              </div>
              <div className="game-info">
                <h4>{explain}</h4>
                <br />
              </div>
              {selectTeam}
              {selectTime}
              <div className="btn-next">
                <Link to="/korean" onClick={this.korean}>
                  
                  <div>
                    <img src={arrow} alt="" />
                  </div>
                </Link>
              </div>
            </div>
          </div>
        </div>
      );
    }
  }
}

export default Setting;



